.software {
  width: 44%;
  font-size: 1.708rem;
  text-transform: uppercase; }
  .software:nth-of-type(2n+1) {
    margin-right: 11%; }
  .software::after {
    content: "";
    display: block;
    width: 100%;
    padding: 4% 0;
    background: #e7e7e7;
    transform: translateY(-100%); }
    @media (max-width: 600px) {
      .software::after {
        margin-right: 0; } }
  @media (max-width: 1440px) {
    .software {
      font-size: 1.25rem;
      line-height: 1.5; } }
  @media (max-width: 1280px) {
    .software {
      font-size: 1.125rem;
      line-height: 1.33; } }
  @media (max-width: 600px) {
    .software {
      width: 100%;
      font-size: 1.5rem;
      text-align: center; } }
  .software__scale-value {
    position: relative;
    margin-top: 0.67rem;
    background: #0270ba;
    padding: 4% 0;
    z-index: 10; }
