.history {
  position: relative;
  padding-left: 8.3%;
  margin-bottom: 0.96rem; }
  .history::before {
    content: "";
    position: absolute;
    top: 0.125rem;
    left: 2%;
    padding: 1.55%;
    background: #000000;
    border-radius: 50%; }
  .history__header {
    margin: 0;
    font-size: 1.71rem;
    line-height: 1;
    text-transform: uppercase; }
    @media (max-width: 1440px) {
      .history__header {
        font-size: 1.25rem;
        line-height: 1; } }
    @media (max-width: 1280px) {
      .history__header {
        font-size: 1.125rem;
        line-height: 1.33; } }
    @media (max-width: 1024px) {
      .history__header {
        font-size: 1.33rem;
        line-height: 1.58; } }
    .history__header_theme-blue {
      color: #0270ba; }
  .history__dates {
    font-weight: normal;
    text-transform: none;
    color: #000000; }
  .history__second-title {
    display: block;
    margin-top: 0.459rem;
    font-weight: normal;
    color: #000000; }
    @media (max-width: 1280px) {
      .history__second-title {
        margin-top: 0; } }
  .history__text {
    margin: 0;
    margin-top: 0.417rem; }
    @media (max-width: 1024px) {
      .history__text {
        font-size: 0.8rem; } }
